@import url('https://fonts.googleapis.com/css?family=Fugaz+One|Sonsie+One|Montserrat');

body {
  background: #79B473;
  color: white;
}

.recipe-title, h4, .index-view-item, .dialog-box  {
  font-family: Fugaz One;
}

.recipe-box-wrapper {
  margin: 20px 15%;
}

.heading {
  font-size: 40px;
  text-align: center;
  margin-bottom: 20px;
  font-family: Sonsie One;
}

.heading div,
.heading i {
    display: inline-block;
    margin-right: 15px;
}

button:hover {
  cursor: pointer;
}

.unstyle-button {
  background: none;
  border: none;
  outline: none;
  font-size: 32px;
  color: #481D24;
}

.unstyle-button:hover {
    color: #79B473;
}

#index-view {
  height: 95px;
  overflow: auto;
  margin-bottom: 5px;
  border: 5px solid #084C61;
  border-bottom: 10px solid #084C61;
  border-top: 10px solid #084C61;
  border-radius: 10px;
}

#index-view .index-view-item:nth-child(odd) {
    color: #79B473;
    background: #084C61;
}

#index-view .index-view-item {
    padding: 7px;
    cursor: pointer;
    text-decoration: none;
    display: block;
}

#index-view .index-view-item:hover {
    color: #481D24
}

a[id*=delete-],
a[id*=edit-] {
    color: #481D24;
    display: inline;
}

a[id*=edit-] {
    position: relative;
    top: -1px;
    left: 8px;
}

a:active,
a:focus {
    color: #481D24;
}

.active {
  background: #084C61;
  border: 2px  solid #084C61;
  color: #79B473;
}

.add-button {
  background: #084C61;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  padding: 5px;
}

.add-button button {
    background: none;
    border: none;
    outline: none;
    font-size: 32px;
    color: #481D24;
}

.recipe-view .recipe-title button {
    background: none;
    border: none;
    outline: none;
    font-size: 32px;
    color: #481D24;
}

.recipe-view .list {
    max-width: 600px;
}

.recipe-view .recipe-body {
    background: #481D24;
    height: 330px;
    overflow-y: scroll;
    padding: 0 10px;
    font-family: Montserrat;
}

.recipe-view .recipe-body h4 {
    letter-spacing: 0.1em;
}

.recipe-view .recipe-title {
    background: #084C61;
    padding: 10px;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}

.recipe-view .recipe-title .title-row {
    display: inline-block;
}

.recipe-view .recipe-title .fa-edit {
    vertical-align: bottom;
}

.recipe-view .recipe-title div:first-of-type {
    margin-right: 10px;
    font-size: 30px;
    font-weight: bold;
}

.dialog-wrap {
  -webkit-box-shadow: 2px 10px 10px 5000px rgba(0, 0, 0, .6) !important;
          box-shadow: 2px 10px 10px 5000px rgba(0, 0, 0, .6) !important;
}

.dialog-box textarea {
    margin-bottom: 10px;
    background: #556670;
    min-width: 300px;
    min-height: 60px;
    resize: vertical;
    color: darken(#084C61, 10%);
    font-weight: bold;
    font-size: 13px;
    overflow: auto;
}

.dialog-box textarea:focus {
    -webkit-box-shadow: 0 0 5px #79B473;
            box-shadow: 0 0 5px #79B473;
    outline: 1px solid #79B473;
    border: 1px solid #79B473;
}

.dialog-box textarea:nth-of-type(3) {
    min-height: 100px;
}

.dialog-box textarea:first-of-type {
    resize: none;
    min-height: 15px;
}

.dialog-box .input-title {
    margin: 5px;
}

.dialog-box button:not(:first-of-type) {
    margin-bottom: 15px;
}

.dialog-box button {
    font-family: Fugaz One;
    font-size: 15px;
    margin-right: 8px;
    background: darken(#084C61, 5%);
    border-radius: 5px;
    background: none;
    border: none;
    outline: none;
    font-size: 32px;
    color: #481D24;
}

.dialog-box .corner-close {
    background: none;
    border: none;
    outline: none;
    font-size: 32px;
    color: #481D24;
    font-size: 24px ;
    color: darken(#084C61, 10%);
    position: absolute;
    top: 5px;
    right: 5px;
}

.dialog-box {
  text-align: center;
  position: fixed;
  width: 500px;
  min-width: 300px;
  top: 50%;
  left: 50%;
  margin-left: -250px;
  margin-top: -200px;
  -webkit-box-shadow: 3px 3px 10px black;
          box-shadow: 3px 3px 10px black;
  border-radius: 15px;
  background: #616196;
  z-index: 1000;
}

@media screen and (max-width: 750px) {
  .recipe-box-wrapper {
    margin: 5%;
  }
  .heading {
    font-size: 30px;
    margin-bottom: 5px;
    margin-top: 5px;
  }
}

@media screen and (max-width: 350px) {
  .dialog-box {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    margin-left: 0;
    margin-top: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
    border-radius: 0;
  }
  .recipe-box-wrapper {
    margin: 0;
  }
  .heading {
    font-size: 24px;
    margin-bottom: 5px;
    margin-top: 5px;
  }
  .recipe-body {
    max-height: 200px;
  }
}




